
.principalDiv{
  position: fixed;
  top: 0;
  left: 0;
 
 justify-items: center;
 align-items: center;
 height: 100vh;
 width: 100%;
 z-index: 30;

}
.popUp{
  background-color: var(--color-primario);
  position: absolute;
  width: 93%;
  height: 95%;
  top: 4%;
  left: 1.3%;
  padding: 30px;
  height: 85%;
  background-color:#1F2044;
  display: grid;
  grid-template-columns: 50%  40%;
  grid-template-rows: 50% 20% 30%;
  justify-content: center;
  align-items: center;
  z-index: 30;
  border-radius: 15px;
  border: solid red 3px;
 
}
.popUp p{
 color: white;
 text-align: justify;
 grid-row: 2;
 grid-column: 2;
 justify-self: start;
 align-self: start;
 
}
.youtube{
  margin-top: 18px;

  grid-row: 1;
  grid-column: 2;
  align-self: self-start;
}

.closeButton{
  grid-row: 1;
  grid-column: 2;
  justify-self: end;
  align-self: start;
  width: 30px;
  position: relative;
  left: 80px;
  top: -10px;
  text-transform: uppercase;
  font-weight: 600;
  border-radius: 0.5rem;
  background: none;
  opacity: 1;
  transition: all 0.3s ease-in-out;
  border: none;
  font-size: 20px;
  text-decoration: none;
  color: white;
  
}
.closeButton:hover{
  background: #ffb400;
 
}

.imgContainer{
 margin-top: 15px;
  width: 85%;
  height: 80%;
  grid-row: 1 / span 2;
  grid-column: 1;
  /* align-self: center;  */
  align-self: self-start;
  box-shadow: 0 0 0.5rem rgb(110, 110, 110);
}
.imgContainer img{
  width: 100%;
  height: 100%;
  align-self: center;
  border-radius: 6px;
  
}
.tecnologiesDic{
  grid-row: 3;
  grid-column: 1/ span 2;
  color: white;
  margin: 0px;
}
.tecnologiesDic h1{
  font-size: 25px;
  margin: 0px;
}
.tecnologiesDic h1 span{
  font-size: 25px;
  margin: 0px;
  color: #ffb400;
}
.imagesDiv{
  height: 85%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;

}
.imagesDiv img{
  height: 75px;
  width: 90px;
  margin:10px;
  display: flex;
  flex-wrap: wrap;
  /* flex-direction: row; */
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 0 1rem rgb(110, 110, 110);
  opacity: 1;
  transition: all 0.3s ease-in-out;
}
.imagesDiv img:hover{
  height: 80px;
  width: 100px;
 
}
.buttonsContainer button{
  cursor: pointer;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 600;
  border-radius: 0.5rem;
  background: none;
  opacity: 1;
  transition: all 0.3s ease-in-out;
  border: none;
  font-size: 20px;
  text-decoration: none;
  color: white;
}
.buttonsContainer button:hover{
  background: #ffb400;
 
}