.principalContainer{
    display: flex;
    width: 100%;
    height: 100vh;   
}
.principalDiv{
    display: flex;
    flex-direction: column;
    height: 93vh;
    background-color: white;
    margin-top: 7vh;
    position: absolute;
    width: 100%;  
    /* justify-content: center; */
    /* align-items: center; */
}
.title{
    display: flex;
    flex-direction: column;
    margin-top: 1px;
    position: relative;
    top: -10px;
    width: 100%;
    align-items: center;
}
.title h1{
    font-size: 2.5rem;
    margin-bottom: 5px;
}
.title h1 span{
    color: #ffb400;;
}
.figureContainer{
    display: flex;
    flex-direction: row;
    align-items: center;
}
.circle{
    width: 13px;
    height: 13px;
    margin: 0px;
    border: solid var(--color-primario) 2px;
    -moz-border-radius: 50%;
    -webkit-border-radius: 50%;
    border-radius: 50%;
    background: transparent;
}
.bar{
    width: 100px;
    border: solid var(--color-primario)1px;
    height: 0px;
    margin: 0px;
}
.countryImage{
    height: 200px;
    width: 350px;
    cursor: pointer;
    margin: 30px;
    margin-top: 10px;
    margin-bottom: 15px;
    border-radius: 20px;
    box-shadow: 0 0 1rem rgb(83, 84, 82);
    opacity: 1;
  transition: all 0.3s ease-in-out;
}
.countryImage:hover{
    height: 210px;
    width: 360px;
}

.projectContainer{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    
    justify-content: center;
    align-items: center;
    margin-top: 80px;
}

.projectContainerInfo {
    display: flex;
    flex-direction: column;
    align-items: center;
    }
.projectContainerInfo h2{
        width: 180px;
        font-size: 1rem;
        text-transform: uppercase;
        font-weight: 500;
        border-radius: 0.5rem;
        background: #ffb400;;
        opacity: 1;
        transition: all 0.3s ease-in-out;
        letter-spacing: 0.75px;
        padding: 0.5rem 0.5rem;
        text-decoration: none;
        color: white;
        margin-top: 30px;
    }
.linksContainer{
    display: flex;
    justify-content: space-between;
}

.linksContainer a{
    cursor: pointer;
    margin-right: 20%;
    text-decoration: none;
    color: var(--color-primario);
    font-size: 15px;
    justify-content: center;
    font-weight: 500;

}    
